<template>
  <div style="padding-top: 0.7rem;">
  	<Detailshead></Detailshead>
  	<list :listArr="Details1"></list>
  	<size :lisize="Text" :lisize2="Text2" :lisize3="Text3"></size>
  	<Detaillist></Detaillist>
  </div>
</template>
<script lang="babel">
import Detailshead from "./common/Detailshead";
import Detaillist from "./common/Detaillist";
import list from "./common/Switchthebar";
import size from "./common/Detaisize";
export default {
	data(){//数据
		return{
			Details1:[],
			youth:[],
			Text:{},
			Text2:{},
			Text3:{}
		}	
	},
	methods:{//js
		
	},
	components:{//注册模块
	Detailshead,
	Detaillist,
	list,
	size
	},
	updated() {
		this.Text=this.youth[0]
		this.Text2=this.Details1[5]
		this.Text3=this.Details1[0]
	},
	beforeMount(){
		var _this = this;

			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/youth.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.youth = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	},
	mounted(){//得到数据的ajax
		var _this = this;

			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/Details1.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.Details1 = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>

</style>


